


::-webkit-scrollbar {
    display: none;
}

.sent{
    padding: 0;
    margin: auto;
    height: 100vh;
    display: flex;   
    flex-direction: column;
    scroll-behavior: smooth;
    scroll-snap-align: start;
}

section{

    padding: 0;
    margin: 0;
    height: 100vh;
    display: flex;
    scroll-snap-align: none;
    flex-direction: column;
    scroll-behavior: smooth;
}


.container{
    scroll-snap-type: y mandatory;
    overflow-y: scroll;
    height: 100vh;
    scroll-behavior: smooth;
}

html{
    scroll-behavior: smooth;
}