.imgcontainer{
    width: 500px;
    height: 470px;
    background-color:crimson;
    border-radius: 21%;
    display:flex;
    align-items: flex-end;
    justify-content: center;
    float: right;
    margin: 64px 0;
}

img{
     height: 55%;
    margin-left: 161px;
}

.wrapper{
    width: 100%;
    height: 100%;
    padding-left: 20px;
    display: flex;
    flex-direction: column;
    margin-top: 99px;
    z-index: 1;
    justify-content: center;
    margin-bottom: 200px;
}

.h111{
    font-size: 60px;
    margin: 10px 0;
    color: white;
    text-align: center;
    margin-bottom: -50px;

}

span{
    font-size: inherit;
    color: crimson;
}
.h222{
    font-size: 35px;
    color: white;
    text-align: center  ;
    margin-bottom: 5px;
}

.h333{
    font-size: 30px;
    color: white;
    text-align: center;
    margin-bottom: -150px;

}

.ityped-cursor{
    animation: blink 1s infinite;
}

@keyframes blink{
       50%{
           opacity: 1;
       } 
    100%{
        opacity: 0;
    }
}
.outline_text1{

    position: relative;
      left: 0;
      right: 0;
      text-align: center;
      top: 16.57%;
      font-size: 215px;
      text-transform: uppercase;
      /* margin-top: 200px; */
      z-index: -1   ;
      -webkit-text-stroke: 1px #1e273d;
      -webkit-text-fill-color: transparent;
      font-weight: 800;
      font-family: "Poppins",sans-serif;
  }

  @media screen and (max-width: 800px){
    .outline_text1{
        display: none;
    }
    .wrapper{
        margin-top: 300px;
    }
    .h111{
        font-size: 45px;
      
        color: white;
        text-align: center;
  
    
    }
    
    span{
        font-size: inherit;
        color: crimson;
    }
    .h222{
        font-size: 35px;
        color: white;
        text-align: center  ;
        margin-top: -170px;

       
    }
    
    .h333{
        font-size: 30px;
        color: white;
        margin-top: 100px;
        text-align: center;
       
    
    }
  }

  @media screen and (max-width: 992px){
    .outline_text1{
        display: none;
    }
  }

  
