.profilepage{
    background-color: rgb(187, 55, 77);
    height: 100vh;
    color: white;
    top: 0;
}

.profile-title{
    margin-top: 200px;
}
.outline_text22{

    position: relative;
      left: 0;
      right: 0;
      text-align: center;
      top: 16.57%;
      font-size: 215px;
      text-transform: uppercase;
      z-index: 1;
      -webkit-text-stroke: 1px #1e273d;
      -webkit-text-fill-color: transparent;
      font-weight: 800;
      font-family: "Poppins",sans-serif;
  }

  @media screen and (max-width: 800px){
    .outline_text1{
        display: none;
    }
    .wrapper{
        margin-top: 300px;
    }
    .h11{
        font-size: 60px;
        margin: -3px 0;
        color: white;
        text-align: center;
  
    
    }
    
    span{
        font-size: inherit;
        color: crimson;
    }
    .h22{
        font-size: 35px;
        color: white;
        text-align: center  ;
        margin-top: -12px;

       
    }
    
    .h33{
        font-size: 30px;
        color: white;
        margin-top: 50px;
        text-align: center;
       
    
    }
  }

  @media screen and (max-width: 992px){
    .outline_text22{
        position: relative;
        left: 0;
        right: 0;
        text-align: center;
        top: 40.57%;
        font-size: 80px;
        text-transform: uppercase;
        z-index: 1;
        -webkit-text-stroke: 1px #1e273d;
        -webkit-text-fill-color: transparent;
        font-weight: 800;
        font-family: "Poppins",sans-serif;
    }
  }

  