.contactpage{
    height: 100vh;
    background-color: rgb(38, 125, 165);
    color: white;
    scroll-behavior: smooth;
    display: flex;
    justify-content: center;
}
.outline_text23{
    position: relative;
    left: 0;
    right: 0;
    text-align: center;
    top: 16.57%;
    font-size: 215px;
    text-transform: uppercase;
    z-index: 1;
    -webkit-text-stroke: 1px #1e273d;
    -webkit-text-fill-color: transparent;
    font-weight: 800;
    font-family: "Poppins",sans-serif;
}
.contact-title{
    margin-top: 200px;
  }

  .card{
      display: flex;
      height: 200px;
      width: 300px;
      margin: 150px auto;
      flex-direction: column;
  }

  .red{
      flex: 0.3;
      background-color: red;
  }

  .blue{
      background-color: aqua;
      flex: 0.3;
  }

  .green{
      background-color: green;
      flex: 0.3;
  }
  @media screen and (max-width: 992px){
    .outline_text23{
        position: relative;
        left: 0;
        right: 0;
        text-align: center;
        top: 40.57%;
        font-size: 80px;
        text-transform: uppercase;
        z-index: 1;
        -webkit-text-stroke: 1px #1e273d;
        -webkit-text-fill-color: transparent;
        font-weight: 800;
        font-family: "Poppins",sans-serif;
    }
  }