.aboutpage{
  height: 100vh;
  /* background-color: rgb(216, 58, 58); */
 /* background: url("https://wallpaperaccess.com/full/141516.jpg") repeat center scroll; */
 opacity: 1;
 background-color: #18881e;
}

.wrapper2{
  width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.left2{
  flex: 0.5;
  overflow: hidden;
  /* background-color: red; */
}
.right2{
  flex: 0.5;
  position: relative;
  background-color: #1c2932;
  border-radius: 6px;

}


 
.about-title{
  margin-top: 170px;
  text-align: end;
}
.h2color{
  color: rgb(55, 144, 204);
}

#verticalline{
  width: 7px;
  border-radius: 21px;
  height: 96vh;
  justify-content: center;
  background-color: rgb(252, 247, 247);
  
  top: 1%;
  position: relative;
}


.boxes{
  margin: 20px;
  background-color: aliceblue;
  overflow: hidden  ;
  height: 200px;
  box-shadow: 0px 12px 18px -6px green;
}

.outline_text3{

  position: relative;
    left: 0;
    right: 0;
    text-align: center;
    top: 16.57%;
    font-size: 215px;
    text-transform: uppercase;
    z-index: 1;
    -webkit-text-stroke: 1px #1e273d;
    -webkit-text-fill-color: transparent;
    font-weight: 800;
    font-family: "Poppins",sans-serif;
}
@media screen and (max-width: 800px){
  .outline_text2{
      display: none;
  }
  .wrapper{
      margin-top: 300px;
  }
  .h11{
      font-size: 60px;
      margin: -3px 0;
      color: white;
      text-align: center;

  
  }
  
  span{
      font-size: inherit;
      color: crimson;
  }
  .h22{
      font-size: 35px;
      color: white;
      text-align: center  ;
      margin-top: -12px;

     
  }
  
  .h33{
      font-size: 30px;
      color: white;
      margin-top: 50px;
      text-align: center;
     
  
  }
}

@media screen and (max-width: 992px){
  .outline_text3{
    position: relative;
    left: 0;
    right: 0;
    text-align: center;
    top: 40.57%;
    font-size: 80px;
    text-transform: uppercase;
    z-index: 1;
    -webkit-text-stroke: 1px #1e273d;
    -webkit-text-fill-color: transparent;
    font-weight: 800;
    font-family: "Poppins",sans-serif;
  }
}

